.wrapper {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.label {
    font-size: 1.2rem;
    font-weight: bold;
}

.input {
    text-transform: capitalize;
    font-size: 2rem;
    outline-offset: 4px;
    outline-color: #212121;
    border: 2px solid;
    border-radius: 4px;
}